
angular.module('wrs', ['beeline'])
.config(function ($stateProvider, $urlRouterProvider) {
  $stateProvider
  .state('tracking', {
    url: '/tracking/{routeId:int}',
  })
  .state('tracking-date', {
    url: '/tracking/{routeId:int}/{date}',
  })

  $urlRouterProvider.otherwise('/tracking')
})
.filter('relativeTime', function() {
  return (date) => {
    if (!date) {
      return '';
    }

    date = typeof(date) == 'string' ? new Date(date)
      : typeof(date) == 'number' ? new Date(date)
      : date;

    var timeDiff = new Date().getTime() - date.getTime();

    return timeDiff < 3600 * 1000 ? (timeDiff / 60000).toFixed(0) + ' minutes ago'
          : timeDiff < 24 * 3600 * 1000 ? (timeDiff / 3600 / 1000).toFixed(0) + ' hours ago'
          : (timeDiff / 3600 / 1000 / 24).toFixed(0) + ' days ago'
  }
})
.controller('TrackingCtrl', function ($scope, MapOptions, TripService,
  CompanyService, RoutesService, $stateParams, $state, $rootScope,
  $timeout, uiGmapGoogleMapApi) {

  var tripPromise = null;
  var routePromise = null;
  var companyPromise = null;

  // icons and all require googleMaps
  uiGmapGoogleMapApi.then((googleMaps) => {
    _.assign($scope.map.busMarker, {
      coords: null,
      icon: {
        url: 'img/busMarker01.png',
        scaledSize: new googleMaps.Size(80, 80),
        anchor: new googleMaps.Point(40, 73),
      },
      options: {},
    })
  })

  $rootScope.$on('$stateChangeSuccess', function(toState, fromState) {
    if (!$stateParams.routeId) return;

    // We have route id
    if ($stateParams.date) {
      var dt = new Date($stateParams.date)
      dt.setHours(0,0,0,0)

      routePromise = RoutesService.getRoute($stateParams.routeId, {
        start_date: dt.getTime(),
        end_date: dt.getTime() + 24*60*60*1000,
        include_trips: true,
      })
    }
    else {
      var dt = new Date()
      routePromise = RoutesService.getRoute($stateParams.routeId, {
        start_date: dt.getTime(),
        end_date: dt.getTime() + 24*60*60*1000,
        include_trips: true,
      })
    }

    tripPromise = routePromise.then((route) => route.trips[0])
    companyPromise = tripPromise.then((trip) => {
      return CompanyService.getCompany(+trip.transportCompanyId);
    });
    tripPromise.then((trip) => { $scope.trip = trip; });
    routePromise.then((route) => { $scope.route = route; });
    companyPromise.then((company) => { $scope.company = company; });

    // Force a ping
    $scope.map.lastPing = null;
    tripPromise.then(reping)

    // Draw the planned route
    $scope.route = null;
    routePromise.then((route) => {
      $scope.route = route;
      var routeLine = _.find($scope.map.lines, {id: 'routeLine'});
      routeLine.path = [];
      _.each(route.path, (point) => {
        routeLine.path.push({
          latitude: point.lat,
          longitude: point.lng
        });
      });
    });

    // Show the stops
    Promise.all([tripPromise, uiGmapGoogleMapApi]).then(([trip, googleMaps]) => {
      $scope.map.markers = [];

      for (let tripStop of trip.tripStops) {
        var coords = tripStop.stop.coordinates.coordinates;
        if (tripStop.canBoard) {
          $scope.map.markers.push({
            id: tripStop.id,
            coords: {latitude: coords[1], longitude: coords[0]},
            icon: {
              url: 'img/MapRoutePickupStop.png',
              scaledSize: new googleMaps.Size(25, 25),
              anchor: new googleMaps.Point(13, 13)
            },
            tripStop: tripStop,
          });
        }
        else if (tripStop.canAlight) {
          $scope.map.markers.push({
            id: tripStop.id,
            coords: {latitude: coords[1], longitude: coords[0]},
            icon: {
              url: 'img/MapRouteDropoffStop.png',
              scaledSize: new googleMaps.Size(25, 25),
              anchor: new googleMaps.Point(13, 13)
            },
            tripStop: tripStop,
          });
        }
      }
    })
  });

  function reping() {
    if (!tripPromise)
      return Promise.resolve(null);

    return tripPromise.then(function(trip) {
      return TripService.DriverPings(trip.id);
    })
    .then((info) => {
      $scope.info = info;
    });
  }

  var pingTimer;
  var pingLoop = function() {
    reping()
    .then((info) => {
      console.log(info)
      pingTimer = $timeout(pingLoop, 5000);
    })
    .catch((err) => {
      console.log(err)
    });
  };
  pingLoop();

  // Draw the icon for latest bus location
  $scope.$watch('info', function() {
    var info = $scope.info;

    if (!info || info.pings.length == 0) {
      return;
    }

    // var busPosition = info.pings[0].coordinates.coordinates;
    $scope.map.lastPing = info.pings[0];
  });

  // Zoom to stop when a particular tripstop is clicked
  $scope.zoomToStop = function(tripStop) {
    var map = $scope.map.control.getGMap()

    map.panTo({
      lat: tripStop.stop.coordinates.coordinates[1],
      lng: tripStop.stop.coordinates.coordinates[0],
    })
    map.setZoom(15)

    $scope.infoStop = tripStop
  }

  // For filtering the stop list
  $scope.infoStop = null;
  $scope.filterBoard = function(tripStop) {
    return tripStop.canBoard
  }
  $scope.filterAlight = function(tripStop) {
    return tripStop.canAlight
  }

  // When markers are clicked
  $scope.selectStop = function(what, event, model) {
    console.log(arguments)
    $scope.infoStop = model.tripStop
  }

  $scope.map = MapOptions.defaultMapOptions({
    lines: [
      {
        id: 'busLine',
        path: [],
        stroke: {
          color: '#333',
          opacity: 1.0,
          weight: 3,
        },
      },
      {
        id: 'routeLine',
        path:[],
        stroke: {opacity: 0},
        icons: [{
          icon: {
            path: 'M 0,-1 0,1',
            strokeOpacity: 0.5,
            scale: 3
          },
          offset: '0',
          repeat: '20px'
        }]
      }
    ],
    busMarker: {
    }
  });
})



